import React, { Component } from "react";
import './home.scss'
import ListView from '../list/list.js'
import imgList from '../../../utils/imgListData.json'

// import { hashHistory } from 'react-router-dom'
// import { HashRouter } from 'react-router-dom'

class Home extends Component {
  constructor(props) {
    super(props);
    console.log("home");
    this.state = { date: new Date(), imgListData: []};
  }
  componentWillMount() {
  }
  imgData(list) {
    this.setState({
      imgListData: list
    })
  }
  
  componentDidMount() {
    let url = 'assets/pc/images/'
    for(let i = 0; i < imgList.length; i++){
      let imgUrl = url + imgList[i].fileName
      imgList[i].imgUrl = imgUrl
    }
    this.imgData(imgList)
  }

  componentWillUnmount(){
  }

  render() {
    return (
      <div className="Home">
        <section className="img-rc">
          <ListView imgListData={this.state.imgListData}/>
        </section>
        <nav className="controller-nav"></nav>
      </div>
    )
  }
}

export default Home


