import React, { Component } from "react";
import './page.scss'

class List extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  handleClick = () => {
    if (this.props.imgSD.isCenter) {
      this.props.rotatePicFn()
    } else {
      this.props.centerFn()
    }
  }
  componentDidMount() {
  }

  render() {
    console.log(this.props.imgSD.inverse)
    let className = 'Page'
    className += this.props.imgSD.isCenter ? ' scale' : ''
    className += this.props.imgSD.inverse ? ' rotate' : ''
    return (
        <div className={className} onClick={this.handleClick}>
          {this.props.children}
        </div>
      )
    }
}

export default List


