import React, { Component } from "react";
import './list.css'

class List extends Component {
  constructor(props) {
    super(props);
    console.log("list");
    this.state = { list: [{name: '', age: '23'}], value: "" };
  }
  
  componentDidMount() {
    // console.log(this.props)
  }
  addList(){
    let bl = this.props.nameFn(this.state.value)
    if (!bl) return false
    let obj = {age: '23'}
    obj.name = this.state.value
    this.setState(prve => ({
      list: [
        obj,
        ...prve.list
      ],
      value: ''
    }))
  }
  inpChange = e => {
    this.setState({
      value: e.target.value
    })
  }
  render() {
    return (
        <div className="List">
          <ul className={this.props.propsStr}>
            {this.state.list.map(function(item, index) {
              return <li key={index}>{item.name}</li>
            })}
          </ul>
          <input className="snapshot-sample" type="text" value={this.state.value} onChange={this.inpChange}/>
          <button type="button" className="btn" onClick={this.addList.bind(this)}></button>
        </div>
      )
    }
}

export default List


