import React, { Component } from "react";
import './list.scss'

import Item from './components/item/item.js'
import Page from './components/navPage/page.js'

class List extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Constant: {
        contentPos: {
          top: 0,
          left: 0
        },
        hPosRange: { // 
          leftSecx: [0, 0],
          rightSecx: [0, 0],
          y: [0, 0]
        },
        vPosRange: { // 
          x: [0, 0],
          topY: [0, 0]
        }
      },
      imgsArrangeArr: []
    };
  }
  getRangeRandom = (low, high) => {
    return Math.floor(Math.random() * (high - low) + low)
  }
  getRangeRandomRotate () {
    return (Math.random() > 0.5 ? '' : '-') + Math.floor(Math.random() * 30)
  }
  // 
  // @param centerIndex 
  rearrage = (centerIndex) => {
    // alert(centerIndex)
    let that = this;
    var imgsArrangeArr = [...that.state.imgsArrangeArr],
        Constant = that.state.Constant,
        contentPos = Constant.contentPos,
        hPosRange = Constant.hPosRange,
        vPosRange = Constant.vPosRange,
        hPRLeftSecx = hPosRange.leftSecx,
        hPRRightSecx = hPosRange.rightSecx,
        hPRYSecx = hPosRange.y,
        vPRTopY = vPosRange.topY,
        vPRX = vPosRange.x,
        
        // 
        imgsArrangeTopArr = [],
        topImgNum = Math.floor(Math.random() * 2),
        topImgIndex = 0,
        //
        imgsArrangeCenterArr = imgsArrangeArr.splice(centerIndex, 1);

        imgsArrangeCenterArr[0] = {
          pos: contentPos,
          rotate: 0,
          isCenter: true
        }

        // 
        topImgIndex = Math.floor(Math.random() * (imgsArrangeArr.length - topImgNum));
        imgsArrangeTopArr = imgsArrangeArr.splice(topImgIndex, topImgNum);
        imgsArrangeTopArr.forEach((item, index) => {
          imgsArrangeArr[index] = {
            pos: {
              top: that.getRangeRandom(vPRTopY[0], vPRTopY[1]),
              left: that.getRangeRandom(vPRX[0], vPRX[1])
            },
            rotate: that.getRangeRandomRotate(),
            inverse: false,
            isCenter: false
          }
        })
        let len = imgsArrangeArr.length,
            bNum = len / 2
        imgsArrangeArr.forEach((item, i) => {
          let empty = null;
          if (i < bNum) {
            empty = hPRLeftSecx
          } else { 
            empty = hPRRightSecx
          }
          imgsArrangeArr[i] = {
            pos: {
              top: that.getRangeRandom(hPRYSecx[0], hPRYSecx[1]),
              left: that.getRangeRandom(empty[0], empty[1])
            },
            rotate: that.getRangeRandomRotate(),
            inverse: false,
            isCenter: false
          }
        });

        if (imgsArrangeTopArr && imgsArrangeTopArr[0]) {
          imgsArrangeArr.splice(topImgIndex, 0, imgsArrangeTopArr[0])
        }

        imgsArrangeArr.splice(centerIndex, 0, imgsArrangeCenterArr[0])
        // console.log(imgsArrangeArr)
        that.setState({
          imgsArrangeArr: imgsArrangeArr
        })
  }
  // 
  rotatePicFn (index) {
    return function () {
      let imgsArrangeArr = [...this.state.imgsArrangeArr]
      // alert(121)
      imgsArrangeArr[index].inverse = !imgsArrangeArr[index].inverse
      this.setState({
        imgsArrangeArr: imgsArrangeArr
      })
      // console.log(this.state.imgsArrangeArr)
    }.bind(this);
  }
  // 
  centerFn (index) {
    return function () {
      this.rearrage(index)
    }.bind(this)
  }
  // 
  componentDidMount() {
    setTimeout(() => {
      // 
      let stageDom = this.refs.ListView,
          stageH = stageDom.scrollHeight,
          stageW = stageDom.scrollWidth,
          halfStageH = stageH / 2,
          halfStageW = stageW / 2;
      let itemsDom = this.refs.Item0,
          imgW = itemsDom.scrollWidth,
          imgH = itemsDom.scrollHeight,
          halfImgW = imgW / 2,
          halfImgH = imgH / 2;
          // console.log(imgW)
      // 
      this.setState({
        Constant: {
          contentPos: {
            left: halfStageW - halfImgW,
            top: halfStageH - halfImgH
          },
          hPosRange: { // 
            leftSecx: [-halfImgW, halfStageW - halfImgW * 3],
            rightSecx: [halfStageW + halfImgW, stageW - halfImgW],
            y: [-halfImgH, stageH - halfImgH]
          },
          vPosRange: { // 
            x: [halfStageW - imgW, halfStageW],
            topY: [-halfImgH, halfStageH - halfImgH * 3]
          }
        }
      })

      this.rearrage(0)
      // console.log(this.state.Constant)
    }, 0)
  }

  render() {
    var itemList = [],
        pageList = [];
    this.props.imgListData.forEach((item, ind) => {
      if(!this.state.imgsArrangeArr[ind]) {
        this.state.imgsArrangeArr.push({
          pos: {
            top: 0,
            left: 0
          },
          rotate: 0,
          inverse: false,
          isCenter: false
        })
      }
      var styleObj = {}
      var zIn = {}
      var str = ''
      if (this.state.imgsArrangeArr[ind].pos) {
        styleObj = {...this.state.imgsArrangeArr[ind].pos}
      }
      if (this.state.imgsArrangeArr[ind].rotate) {
        (['-moz-', '-ms-', '-webkit-', '']).forEach((val) => {
          styleObj['transform'] = 'rotateZ(' + this.state.imgsArrangeArr[ind].rotate + 'deg)'
        })
      }
      if (this.state.imgsArrangeArr[ind].isCenter) {
        zIn = {zIndex: 111}
        // str = <i className="iconfont">&#xe609;</i>
        // str = <embed src="../../../assets/m/images/back.svg" width="30" height="30"/>
        str = <svg className="icon" aria-hidden="true">
                <use xlinkHref="#icon-fanhui1"></use>
              </svg>
      }
      // console.log(styleObj)
      pageList.push(<Page key={ind} imgSD={this.state.imgsArrangeArr[ind]} rotatePicFn={this.rotatePicFn(ind)} centerFn={this.centerFn(ind)}>
        {str}
      </Page>)
      itemList.push(<div className="items" style={zIn} key={ind} ref={'Item' + ind}><Item imgSD={this.state.imgsArrangeArr[ind]} styleObj={styleObj} rotatePicFn={this.rotatePicFn(ind)} centerFn={this.centerFn(ind)} item={item}/></div>)
    });
    return (
        <div className="ListView" ref="ListView">
          {itemList}
          <div className="navPageView">
            {pageList}
          </div>
        </div>
      )
    }
}

export default List


