import React, { Component } from "react";
import './home.scss'
import ListView from '../list/list.js'

// import { hashHistory } from 'react-router-dom'
// import { HashRouter } from 'react-router-dom'

class Home extends Component {
  constructor(props) {
    super(props);
    console.log("home");
    this.state = { date: new Date() };
  }

  componentDidMount() {
    this.setDate = setInterval(() => {
      this.timeD()
    }, 1000)
    // console.log(this.props)
  }

  componentWillUnmount(){
    clearInterval(this.setDate)
  }

  timeD(){
    this.setState({
      date: new Date()
    })
  }
  toList(){
    this.props.history.goBack()
  }
  nameFn = (val) => {
    if (!val) return false
    alert(val)
    return true
  }
  render() {
    return (
      <div className="Home">
        <div onClick={this.toList.bind(this)}>{this.state.date.toLocaleTimeString()}</div>
        <div>
          <ListView propsStr='className' nameFn={this.nameFn}/>
        </div>
      </div>
    )
  }
}

export default Home


