import React from 'react';
import ReactDOM from 'react-dom';
import Browser from './utils/Browser.js'
import './index.css';
import './assets/reset.css'
import './assets/pc/style/common.scss'
// import App from './page/App';
import RouteMap from './router/index';

import registerServiceWorker from './registerServiceWorker';
import { fontSize } from './assets/m/js/font_size.js'
require('./assets/icon-font/iconfont.js')

// import { HashRouter } from 'react-router-dom'

const browser = new Browser()
if (browser.device === 'PC') {
  document.body.setAttribute('class', 'PC')
  document.documentElement.style.overflow = 'visible'
} else {
  require('./assets/m/style/H.css')
  document.body.setAttribute('class', 'M')
  fontSize(750)
}
// if (location.hash.indexOf('/404') === 0) {
