//   const pcPath = location.hash.replace('/m', '')
//   console.log(pcPath)
//   window.location.href = location.origin + '/' + pcPath
// } else if (location.hash.indexOf('/m') !== 0 && browser.device !== 'PC') {
//   const mPath = location.hash.replace('/#/', '#/m/')
//   console.log(mPath)
//   window.location.href= location.origin + '/' + mPath
// }

ReactDOM.render(<RouteMap device={browser.device}/>, document.getElementById('root'));
registerServiceWorker();



