import React from 'react'
// import { Router, Route, IndexRoute } from 'react-router'
import './index.scss' 

import { Route, HashRouter } from 'react-router-dom'
import App from '../page/App.js'
// pc-view  --start
import PcHome from '../page/pc/home/home.js'
import PcList from '../page/pc/list/list.js'
import PcAotuList from '../page/pc/AotuList/AotuList.js'
// pc-view  --end

// m-view  --start
import MHome from '../page/m/home/home.js'
import MList from '../page/m/list/list.js'
import MAotuList from '../page/m/AotuList/AotuList.js'
// m-view  --end
 
class RouteMap extends React.Component {
  updateHandle() {
    alert('aaaa')
    console.log(this.props.location)
    console.log('router')
  }
  routerView = () => {
    if (this.props.device === 'PC') {
      return (
        <div className="routerView">
          <Route exact path='/' component={PcHome}/>
          <Route path='/list' component={PcList}/>
          <Route path='/detail' component={PcAotuList}/>
        </div>
      )
    } else {
      return (
        <div className="routerView">
          <Route exact path='/' component={MHome}/>
          <Route path='/list' component={MList}/>
          <Route path='/detail' component={MAotuList}/>
        </div>
      )
    }
  }
  render() {
    return (
      <HashRouter onUpdate={this.updateHandle.bind(this)}>
        <App>
          {this.routerView()}
          
          {/* <div>
            <Route path='/home' component={Home}/>
            <Route path='/list' component={List}/>
            <Route path='/detail' component={AotuList}/>
          </div> */}
        </App>
      </HashRouter>
    )
  }
}
 
export default RouteMap


