import React, { Component } from "react";
import './item.scss'

class List extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  handleClick = () => {
    if (this.props.imgSD.isCenter) {
      this.props.rotatePicFn()
    } else {
      this.props.centerFn()
    }
  }
  componentDidMount() {
  }

  render() {
    // console.log(this.props.imgSD)
    let className = 'List'
    className += this.props.imgSD.inverse ? ' rotate' : ''
    return (
        <div className={className} style={this.props.styleObj} onClick={this.handleClick}>
          <div className="gibView">
            <div className="imgView"><img src={require("../../../../../" + this.props.item.imgUrl)} alt={this.props.item.title}/></div>
            <div className="title">{this.props.item.title}</div>
          </div>
          <div className="mask">{this.props.item.desc}</div>
        </div>
      )
    }
}

export default List


